/* thematic color variables */
.bota-light {
  --front:rgb(37,38,34);
  --strong:rgb(148,67,13);
  --mellow:rgb(200,172,134);
  --back: rgb(241,244,227);
}
.bota-dark {    
  --front: rgb(241,244,227);
  --strong:rgb(148,67,13);
  --mellow:rgb(200,172,134);
  --back:rgb(37,38,34);
}
.bota-strong {
  --front:rgb(37,38,34);
  --strong:rgb(200,172,134);
  --mellow: rgb(241,244,227);
  --back:rgb(148,67,13);
}

/* flexboxing */
.papabear {
  display: flex;
}

.babybear {
  flex-wrap: wrap;
  flex-direction: row;
}

/* links */
.link {
  margin: 20px;
}

/* general rules, setting theme */
* {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 300;
  font-size: medium;
  background-color: var(--back);
  color: var(--front);
  text-decoration: none;
  list-style-type: none;
}

h1 {
  font-weight: 500;
  font-size: xx-large;
}

h2 {
  font-weight: 300;
  font-size: xx-large;
}

h3 {
  font-weight: 500;
  font-size: medium;
}

h4 {
  font-weight: 300;
  font-size: medium;
}

h5 {
  font-weight: 300;
  font-size: small;
}

/* styling by section */
.botalogo {
  width: 300px;
}